import React, { useState, useRef, useEffect } from 'react';
import './App.css';
import TodoList from './TodoList';
import { v4 as uuidv4 } from 'uuid';

const   local_storage_key = 'todoApp.todos'

function App() {
  const[todos, setTodos] = useState([])
  const todoNameRef = useRef() 
   
  useEffect(() =>{
    const storedTodos = JSON.parse(localStorage.getItem(local_storage_key))
    if(storedTodos) setTodos(storedTodos)
  }, [])

  useEffect(() =>{
    localStorage.setItem(local_storage_key, JSON.stringify(todos))
  }, [todos])

  function toggleTodo(id){
    const newTodos = [...todos]
    const todo = newTodos.find(todo => todo.id === id)
    todo.complete = !todo.complete
    setTodos(newTodos)
  }

  const handleAddTodo = (e) => {
    const name = todoNameRef.current.value;
    if(name === '') return
    setTodos(prevTodos => {
      return [...prevTodos, {id: uuidv4(), name: name, complete: false}]
    })
    todoNameRef.current.value=null;
  }

  function handleClearTodo(){
    const newTodos = todos.filter(todo=> !todo.complete)
    setTodos(newTodos)
  }

  
  return (
    <>
     
      <div className="App">
        <div><center>{todos.filter(todo => !todo.complete).length} left to do</center></div>
        <center><input ref={todoNameRef} type="text" id='text' /></center>
        <div className="b"><center>
          <button type='submit' onClick={handleAddTodo} id='add'>Add Todo</button>
          <button type='submit' onClick={handleClearTodo} id='clear'>Clear Complete</button></center>
        </div>
        <TodoList todos={todos} toggleTodo={toggleTodo} id='todo'></TodoList>
      </div>
  
    </>
  );





}

export default App;
