import React from 'react';
import './App.css';
export default function TodoComponent({ todo,toggleTodo }) {
    function handleTodoClick() {
        toggleTodo(todo.id)
    }
    return (
        <div>
            <label>
                <input type="checkbox" checked={todo.complete} onChange={handleTodoClick} id='todo'/>
                {todo.name}
            </label>
        </div>
    )
}
